@import url('https://fonts.googleapis.com/css?family=Nunito+Sans');
:root {
  --blue: #0e0620;
  --white: #fff;
  --green: #2ccf6d;
}

.container {
  color: black;
}
.container button {
  font-family: 'Nunito Sans';
}

.container ul {
  list-style-type: none;
  -webkit-padding-start: 35px;
  padding-inline-start: 35px;
}

.container svg {
  width: 100%;
}

.container h1 {
  font-size: 7.5em;
  margin: 15px 0px;
  font-weight: bold;
}

.container h2 {
  font-weight: bold;
}

.container .hamburger-menu {
  position: absolute;
  top: 0;
  left: 0;
  padding: 35px;
  z-index: 2;
}
.container .hamburger-menu button {
  position: relative;
  width: 30px;
  height: 22px;
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
}
.container .hamburger-menu button span {
  position: absolute;
  height: 3px;
  background: #000;
  width: 100%;
  left: 0px;
  top: 0px;
  transition: 0.1s ease-in;
}
.container .hamburger-menu button span:nth-child(2) {
  top: 9px;
}
.container .hamburger-menu button span:nth-child(3) {
  top: 18px;
}
.container .hamburger-menu [data-state='open'] span:first-child {
  transform: rotate(45deg);
  top: 10px;
}
.container .hamburger-menu [data-state='open'] span:nth-child(2) {
  width: 0%;
  opacity: 0;
}
.container .hamburger-menu [data-state='open'] span:nth-child(3) {
  transform: rotate(-45deg);
  top: 10px;
}

.container .btn {
  z-index: 1;
  overflow: hidden;
  background: transparent;
  position: relative;
  padding: 8px 50px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1em;
  letter-spacing: 2px;
  transition: 0.2s ease;
  font-weight: bold;
  margin: 5px 0px;
}
.container .btn {
  border: 4px solid var(--green);
  color: var(--blue);
}
.container .btn:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  background: var(--green);
  z-index: -1;
  transition: 0.2s ease;
}
.container .btn:hover {
  color: var(--white);
  background: var(--green);
  transition: 0.2s ease;
}
.container .btn:hover:before {
  width: 100%;
}

@media screen and (max-width: 768px) {
  body {
    display: block;
  }

  .container {
    margin-top: 70px;
    margin-bottom: 70px;
  }
}
